/* Css file */
.about__container {
    padding: 20px;
    border-radius: 15px;
    background-color: white;
    margin: 0 auto;
    margin-top: 100px;
    width : 100%;
    max-width: 400px;
    font-weight: 300;
    box-shadow: 0 13px 27px -5px rgba(50, 50 93, 0.25),
                0 8px 16px -8px rgba(0, 0, 0, 0.3),
                0 -6px 16px -6px rgba(0, 0, 0, 0.25);
}

.about__container span:first-child {
    font-size: 25px;
}

.about__container span:last-child {
    display : block;
    font-size: 15px;
}

